body {
  margin: 2rem auto;
  font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
  font-weight: 300;
  background-color: cornsilk;
  color: darkslategray;
}

section {
  margin: 3rem auto;
  max-width: 70rem;
  padding: 0 1rem;
}

h1,
h2 {
  margin: 1rem 0 1.5rem;
  text-align: center;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.4rem;
}

article h2 {
  text-align: left;
  padding: 0 0.5rem;
  margin-bottom: .5rem;
}

p,
pre {
  line-height: 1.4;
}

p {
  font-size: 1.2rem;
  padding: 0.5rem;
}

pre {
  font-size: 1.3rem;
  font-family: monospace;
}

.row,
.cell {
  margin-bottom: 1.5rem;
}

.cell,
.gcell {
  background-color: azure;
  border: 1px solid darkcyan;
}

.cell p,
.gcell p {
  text-align: center;
}

.nested aside,
.nested main {
  border: dashed 1px darkcyan;
}

@media (min-width: 46em) {
  .row {
    margin: 0 auto;
    max-width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .row.even.one-col .cell {
    width: 100%;
  }
  .row.even.two-col .cell {
    width: 49.5%;
  }
  .row.even.three-col .cell {
    width: 100%;
  }
  .row.even.four-col .cell {
    width: 100%;
  }
  .cell.two-twelve {
    width: 100%;
  }
  .cell.three-twelve {
    width: 100%;
  }
  .cell.five-twelve {
    width: 100%;
  }
  .cell.eight-twelve {
    width: 100%;
  }
  .cell.nine-twelve {
    width: 100%;
  }
  .cell.two-eleven {
    width: 100%;
  }
  .cell.four-eleven {
    width: 100%;
  }
  .cell.one-eleven {
    width: 100%;
  }
  .nested {
    margin: 0 auto;
    max-width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .nested aside {
    width: 32.66667%;
  }
  .nested main {
    width: 66.33333%;
  }
  .gcell {
    width: 49%;
  }
}

@media (min-width: 60em) {
  .row {
    margin: 0 auto;
    max-width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .row.even.one-col .cell {
    width: 100%;
  }
  .row.even.two-col .cell {
    width: 49.5%;
  }
  .row.even.three-col .cell {
    width: 32.66667%;
  }
  .row.even.four-col .cell {
    width: 24.25%;
  }
  .cell.two-twelve {
    width: 15.83333%;
  }
  .cell.three-twelve {
    width: 24.25%;
  }
  .cell.five-twelve {
    width: 41.08333%;
  }
  .cell.eight-twelve {
    width: 66.33333%;
  }
  .cell.nine-twelve {
    width: 74.75%;
  }
  .cell.two-eleven {
    width: 17.36364%;
  }
  .cell.four-eleven {
    width: 35.72727%;
  }
  .cell.one-eleven {
    width: 8.18182%;
  }
  .gcell {
    width: 49%;
  }
}
